  async createModelVersion(modelId: string, versionData: { [key: string]: any }): Promise<ApiResponse<Model>> {
    // API
    // modelIdID
    const match = modelId.match(/^(.*_v\d+)(?:_v\d+)*$/)
    const baseId = match ? match[1].split('_v')[0] : modelId.split('_v')[0]
    const randomVersion = Math.floor(Math.random() * 100)
    
    return Promise.resolve({
      success: true,
      data: {
        id: `${baseId}_v${randomVersion}`,
        name: versionData.name || 'New Model Version',
        version: `v${randomVersion}`,
        description: '',
        status: 'ready',
        created_at: new Date().toISOString(),
        updated_at: new Date().toISOString()
      },
      message: ''
    })
  }

  async importModel(modelData: { name: string }, file: File): Promise<ApiResponse<Model>> {
    const formData = new FormData()
    formData.append('name', modelData.name)
    formData.append('file', file)

    try {
      const response = await http.request({
        url: '/api/models/import',
        method: 'POST',
        data: formData,
        //  multipart 
        headers: {},
      })

      return response as unknown as ApiResponse<Model>
    } catch (error) {
      console.error('API request failed for /api/models/import/', error)
      return {
        success: false,
        error: error instanceof Error ? error.message : 'Unknown error'
      }
    }
  }


