  async getModelTrainingStatus(taskId: string): Promise<ApiResponse<TrainingStatusResponse>> {
    // taskId
    const progress = parseInt(taskId.slice(-2)) || 0
    const currentStep = Math.floor((progress / 100) * 100)
    const status = progress >= 100 ? 'completed' : 'training'
    const stage = progress < 30 ? 'initialization' : progress < 70 ? 'training' : 'evaluation'
    
    // API
    return Promise.resolve({
      success: true,
      data: {
        task_id: taskId,
        model_id: 'agriculture_classification_v1',
        status,
        progress: Math.min(progress + 10, 100), // 10%
        stage,
        current_step: Math.min(currentStep + 10, 100),
        total_steps: 100,
        started_at: new Date(Date.now() - progress * 1000).toISOString(),
        metrics: {
          accuracy: Math.min(0.98, (progress + 10) / 100),
          loss: Math.max(0.05, 1.0 - (progress + 10) / 100)
        }
      },
      message: ''
    })
  }

