import { http } from '@/lib/api-client'
import { ApiResponse, Model } from '@/services/api'
import { apiClient } from './api'

export async function fetchModels(): Promise<Model[]> {
  const res = await http.get('/api/models')
  // axios
  if (res && typeof res === 'object' && 'data' in res) {
    const responseData = (res as any).data
    // ApiResponse
    if (Array.isArray(responseData)) {
      return responseData
    }
    // ApiResponse
    const apiRes = responseData as ApiResponse<Model[]>
    if (!apiRes.success) throw new Error(apiRes.error || '')
    return apiRes.data || []
  }
  // 
  if (Array.isArray(res)) {
    return res
  }
  throw new Error('')
}

export async function fetchModel(id: string): Promise<Model> {
  // ID_v1
  //  agriculture_classification_v1_v56 ID agriculture_classification_v1
  const match = id.match(/^(.*_v\d+)(?:_v\d+)*$/)
  const originalId = match ? match[1] : id
  
  // apiClientAPI
  const res = await apiClient.getModel(originalId)
  if (!res.success || !res.data) throw new Error(res.error || '')
  return res.data
}

export async function fetchModelVersions(id: string): Promise<Model[]> {
  // apiClientAPI
  const res = await apiClient.getModelVersions(id)
  if (!res.success) throw new Error(res.error || '')
  return res.data || []
}
